import React from 'react'
import './index.less'
import NoneData from 'components/NoneData/all-none'
import {withRouter} from 'react-router-dom'
import connect from 'connect'
import { List, TextareaItem, NavBar, ImagePicker, Card, Flex } from 'antd-mobile';
const data = [];




@connect
@withRouter
class Home extends React.Component {
    constructor(props){
        super(props);
        
        this.state = {
          navTop: false,
          files: data,
          multiple: false,
        }

        this.$tab = null;
        this.offsetTop = 0;
    }

    
    componentDidMount () {
       setTimeout(() => {
             this.setState({
               data: ['AiyWuByWklrrUDlFignR', 'TekJlZRVCjLFexlOCuWn', 'IJOtIlfsYdTyaDTRVrLI'],
             });
           }, 100);
    }


    onChange = (files, type, index) => {
      console.log(files, type, index);
      this.setState({
        files,
      });
    }
    onSegChange = (e) => {
      const index = e.nativeEvent.selectedSegmentIndex;
      this.setState({
        multiple: index === 1,
      });
    }
    handleScroll(){
        let sTop = document.documentElement.scrollTop || window.pageYOffset || document.body.scrollTop;

        if(!this.state.navTop && sTop >= this.offsetTop){
           this.setState({
             navTop: true
           })
        }

        if(sTop < this.offsetTop){
           this.setState({
             navTop:false
           })
        }
      }

    pingfen() {
     alert('1')
    }

    renderTabBar(props) {
      return (<div>
        {({ style }) => <div style={{ ...style, zIndex: 1 }}><Tabs.DefaultTabBar {...props} /></div>}
      </div>);
    }

    onChange2 = (val) => {
        // console.log(val);
        this.setState({ val });
      }
      onChange1 = (val1) => {
        // console.log(val);
        this.setState({ val1 });
      }


    render () {
      
      const data22 = [
            { value: 0, label: 'Ph.D.' },
            { value: 1, label: 'Bachelor' },
            { value: 2, label: 'College diploma' },
          ];
        const Item = List.Item;
        const Brief = Item.Brief;
        const { files } = this.state;
       
        return (
            <div className="myreview-container">
              <NavBar
                   mode="light"
                   icon={<img src={require('../../images/back.png')} alt="LOGO" className="back"  onClick={this.props.history.goBack}/>}
                   rightContent={<span>发布</span>}
                 >评价</NavBar>
              <List>
                        <TextareaItem
                          rows={5}
                          placeholder='觉得作品怎么样？快写下来'
                        />
              </List>
              <Card>
                    <Card.Header
                      title={<span style={{lineHeight: '0.55rem', fontSize: '0.14rem', color:'#666', marginTop: '0.1rem'}}>综合评价</span>}
                      extra={<Flex><Flex.Item><img src={require('../../images/pj_xing1.png')} alt="xing" className="xing" /></Flex.Item>
                      <Flex.Item><img src={require('../../images/pj_xing1.png')} alt="xing" className="xing" /></Flex.Item>
                      <Flex.Item><img src={require('../../images/pj_xing1.png')} alt="xing" className="xing" /></Flex.Item>
                      <Flex.Item><img src={require('../../images/pj_xing1.png')} alt="xing" className="xing" /></Flex.Item>
                      <Flex.Item><img src={require('../../images/pj_xing2.png')} alt="xing" className="xing" /></Flex.Item>
                      </Flex>}
                    />
              </Card>
              <List>
              <ImagePicker
                files={files}
                onChange={this.onChange}
                onImageClick={(index, fs) => console.log(index, fs)}
                selectable={files.length < 5}
                multiple={true}
              />
              </List>
            </div>
        )
    }
}
export default Home


